import React from 'react'
import { makeStyles } from "@material-ui/core/styles";
import Style from './SideBarStyle';
import SideBarMenuItems from "../../Constant"
import StrokeStyle from '../StrokeStyle'
import SelectionGroupPopup from '../SelectionGroupPopup'
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import menuState from '../MenuState'
import ShapeLibrary from '../ShapeLibrary'
import TextModePopup from '../TextModePopup'
import AppInput from '../AppInput'
import { FaMousePointer, FaHandRock } from 'react-icons/fa';
import keyboardInput from '../KeyboardInput'
import command from '../Command'

const useStyles = makeStyles(Style);

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
      () => { stateRef.current = state },
      [state]
    )
    return [state, stateRef, setState];
}

const SideBarMenu = () => {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [placement, setPlacement] = React.useState();
    const [subMenuIcons, setSubMenuIcons] = React.useState();
    const [counter, counterRef, setCounter] = useRefState(0);
    const classes = useStyles();

    const reload = () => {
        setCounter(counterRef.current + 1);
    }

    const handleKeyDown = (event) => {
        if (!AppInput.isEditingText()) {
            let key = event.key.toLowerCase();
            var handled = false;
            if (key == 'escape') {
                if (menuState.hasPopup()) {
                    menuState.removePopup();
                    handled = true;
                } else if (menuState.selected == menuState.menus.laser ||
                    menuState.selected == menuState.menus.erase ||
                    menuState.selected == menuState.menus.text) {
                    menuState.handleMenuClick(menuState.menus.selection);
                    handled = true;
                }

                if (handled) {
                    event.preventDefault();
                    AppInput.ignoreNextClick(false);
                    reload();
                    return;
                }
            }
            keyboardInput.handleKeyDown(event);
            if (command.inputModeChanged) {
                command.inputModeChanged = false;
                reload();
            }
        }
    };
    
    React.useEffect(() => {
        document.addEventListener('keydown', handleKeyDown);
    
        // cleanup this component
        return () => {
            document.removeEventListener('keydown', handleKeyDown);
        };
    }, []);
    
    const handleClick = (event, subMenus, newPlacement,id) => {
        if (id == menuState.menus.eraseAll) {
            menuState.handleMenuClick(id);
            return;
        }
        menuState.handleMenuClick(id);
        setAnchorEl(event.currentTarget);
        var willOpen = menuState.popup != null;

        if (willOpen && (id == menuState.menus.draw || id == menuState.menus.smartDraw || id == menuState.menus.shapes)) {
            AppInput.ignoreNextClick(true);
        } else {
            AppInput.ignoreNextClick(false);
        }

        setPlacement(newPlacement);
        setSubMenuIcons(subMenus)
        reload();
    };

    const handleClickAway = () => {
        if (menuState.popup != null) {
            menuState.removePopup();
            reload();
        }
    }

    const setSelectionMode = (val) => {
        menuState.handleMenuClick(val);
        reload();
    };
    
    const getIconAndName = (sidebar, classes) => {
        var {icon, name} = sidebar;
        const {menuItem} = classes;
        if (sidebar.id == menuState.menus.selectionGroup) {
            if (menuState.selectionGroupVal == menuState.menus.selection) {
                icon = <FaMousePointer size="1.2em" color="#000000b8"/>;
                name = "Select";
            } else {
                icon = <FaHandRock size="1.2em" color="#000000b8"/>;
                name = "Move";
            }
        }
        return (
            <div>
                <div className={menuItem + ' ' + 'sideMenuIcons'}>
                    {icon}
                </div>
                <div className={menuItem +' '+ 'menuItems'}>{name}</div>
            </div>
        )
    }
    return (
        <ClickAwayListener onClickAway={handleClickAway}>
        <div>
           { (menuState.popup === menuState.menus.selectionGroup) && <SelectionGroupPopup
                open={true}
                anchorEl={anchorEl}
                placement={placement}
                setSelectionMode={setSelectionMode}
            /> }
           { (menuState.popup === menuState.menus.draw || menuState.popup === menuState.menus.smartDraw) && <StrokeStyle
                open={true}
                anchorEl={anchorEl}
                placement={placement}
                subMenuIcons ={subMenuIcons}
            /> }
            { menuState.popup === menuState.menus.text && <TextModePopup
                open={true}
                anchorEl={anchorEl}
                placement="right-end"
            /> }
            { menuState.popup === menuState.menus.shapes && <ShapeLibrary
                open={true}
                anchorEl={anchorEl}
                placement="right-end"
                subMenuIcons ={subMenuIcons}
            /> }
            
            <div className={`${classes.sideBar} side_menus`}>
                {SideBarMenuItems.map((sidebar, index) => {
                    var itemClass = classes.sideBarMenu +' '+ 'sideBarMenu';
                    if (sidebar.id === menuState.menus.shapes && menuState.popup === menuState.menus.shapes) {
                        itemClass = classes.sideBarMenu +' '+ 'sideBarMenu selected';
                    }
                    if (sidebar.id === menuState.selected) {
                        itemClass = classes.sideBarMenu +' '+ 'sideBarMenu active';
                    }
                    return (
                        <div className={itemClass} key={index} onClick={(event)=>handleClick(event, sidebar.subMenus,'right',sidebar.id)} >
                            {getIconAndName(sidebar, classes)}
                        </div>
                    )
                })}

            </div>
        </div>
        </ClickAwayListener>
        
    )
}
export default SideBarMenu